<template>
  <div>
    <mu-appbar style="width: 100%;" :color="$store.state.color" class="header">
      <a @click="()=>{
          $router.go(-1)
        }">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span>{{$route.query.station}}</span>
    </mu-appbar>
    <iframe
      :style="heightWindow"
      class="bimHeight"
      :src="bimUrl+'/?MN='+$route.query.MN"
      style
      frameborder="0"
    ></iframe>
  </div>
</template>
<script>
import { bimUrl } from "../../../static/js/requestUrl";
const heightDevice =
  document.documentElement.clientHeight || document.body.clientHeight;
export default {
  data() {
    return {
      bimUrl: bimUrl,
      heightWindow: "height:" + (heightDevice-56) + "px"
    };
  },
  mounted() {}
};
</script>
<style scoped>
.bimHeight {
  width: 100%;
}
</style>


